import { useState } from 'react';
import Element from './components/Element';
import { FormContext } from './FormContext';
import AceEditor from 'react-ace';
import 'ace-builds/src-noconflict/mode-json';
import 'ace-builds/src-noconflict/theme-github';
import './App.css'; // For split-screen styling

function App() {
  const [jsonText, setJsonText] = useState(JSON.stringify(require('./formElement.json')[0], null, 2));
  const [formState, setFormState] = useState(() => JSON.parse(jsonText));
  const [error, setError] = useState(null);
  const [isSubmitted, setIsSubmitted] = useState(false); // Track form submission status

  const handleJSONChange = (newText) => {
    setJsonText(newText);
    try {
      const parsed = JSON.parse(newText);
      setFormState(parsed);
      setError(null);
    } catch (err) {
      setError('Invalid JSON format');
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(formState);

    // Simulate form submission success
    setIsSubmitted(true);

    // Clear the success message after a delay
    setTimeout(() => setIsSubmitted(false), 3000);
  };

  const handleChange = (id, event) => {
    const updatedFormState = { ...formState };
    updatedFormState.fields.forEach((field) => {
      if (field.field_id === id) {
        switch (field.field_type) {
          case 'checkbox':
            field.field_value = event.target.checked;
            break;
          default:
            field.field_value = event.target.value;
        }
      }
    });
    setFormState(updatedFormState);
  };

  const { fields, page_label } = formState ?? {};

  return (
    <div className="App split-screen">
      {/* Left Side: JSON Editor */}
      <div className="editor-pane">
        <h3 className="pane-title">JSON Editor</h3>
        <AceEditor
          mode="json"
          theme="github"
          value={jsonText}
          onChange={handleJSONChange}
          name="json-editor"
          editorProps={{ $blockScrolling: true }}
          width="100%"
          height="100%"
        />
        {error && <p className="error-message">{error}</p>}
      </div>

      {/* Right Side: Form Preview */}
      <div className="form-pane">
        <FormContext.Provider value={{ handleChange }}>
          <div className="form-container">
            <h3 className="text-2xl font-bold text-gray-800 mb-6 text-center">{page_label}</h3>
            <form onSubmit={handleSubmit} className="space-y-4">
              {fields
                ? fields.map((field, i) => (
                    <div key={i} className="mb-4">
                      <Element field={field} />
                    </div>
                  ))
                : null}
              <button
                type="submit"
                className="w-full bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 transition"
              >
                Submit
              </button>
            </form>
            {isSubmitted && (
              <p className="mt-4 text-green-500 text-center">Form submitted successfully!</p>
            )}
          </div>
        </FormContext.Provider>
      </div>
    </div>
  );
}

export default App;
